.project2-list-item{
    display: flex;

    height: 3rem;
    font-size: 24px;
    & img{
        margin-left: 1rem;
        margin-bottom: 1rem;

    }
}

.project2-list-item:hover{
    color: #3cccbd;
    cursor: pointer;
    & img{
        animation: myAnim 2s ease 0s 1 normal forwards;
    }
}

.projects2-wrapper{
    display: flex;
    flex-direction: column;
    justify-content: start;
    align-content: start;
    text-align: start;
}

@keyframes myAnim {
    0%,
    100% {
        transform: rotate(0deg);
        transform-origin: 100% 50%;
    }

    10% {
        transform: rotate(2deg);
    }

    20%,
    40%,
    60% {
        transform: rotate(-4deg);
    }

    30%,
    50%,
    70% {
        transform: rotate(4deg);
    }

    80% {
        transform: rotate(-2deg);
    }

    90% {
        transform: rotate(2deg);
    }
}